<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1">Выставленные счета</div>
            </div>
            <div class="main-title__right">
                <span class="i i-info i--dark-trans mr4"></span>
                <span class="t text-color-grey">Полный список выставленных вам счетов</span>
            </div>
        </div>




        <section class="container" v-if="user.permission.indexOf(9) !== -1 ">
            
            <div class="container__full">
                
                <div class="buh-invoices" v-if="bills.length > 0">
                    <div class="item">
                        <div class="item__title">
                            <span class="item__num">номер</span>
                            <span class="item__date">выставлен</span>
                            <span class="item__sum">сумма</span>
                            <span class="item__stat">статус</span>
                        </div>
                    </div>
                    
                    
                    <div class="item" v-for="bill in bills">
                        <a class="item__link" href="javascript:void(0);" @click="openPartnerBill(bill)">
                            <span class="item__num">{{ bill.number }}</span>
                            <span class="item__date">{{ bill.dateExposed }}</span>
                            <span class="item__sum">{{ bill.price }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            <span class="item__stat" :class="['--']+bill.color">
                                <span class="i" :class="['i-']+bill.icon"></span>
                                <span class="t">{{ bill.status }}</span>
                            </span>
                        </a>
                    </div>
                </div>
                <!-- end buh-coupons -->
                

                <div class="wrapper-content mt32" v-else>
                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <loader v-if="isLoad"></loader>
                            <div class="text-size-l text-bolder text-color-grey mt16" v-else>У вас пока нет ни одного счёта...</div>
                        </div>

                        <div class="mini-zems"></div>
                    </div>
                    <!-- end sect-blank -->
                </div>
                
            </div>
            <!-- end container__full -->
            
        </section>
        <!-- end container -->
        <section class="container" v-if="user.permission.length > 0 && user.permission.indexOf(9) === -1 ">
            <div class="container__full">
                <div class="wrapper-content">
                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <div class="text-size-l text-bolder text-color-red mt16">
                                У вас нет прав <br> доступа в этот раздел
                            </div>
                        </div>
                    </div>
                    <!-- end sect-blank -->
                </div>
            </div>
        </section>
        <popup :is-set-styled="true" :show="popups.partnerBill.show" :name="popups.partnerBill.name" :width="992">
            <partner-bill :bill="currentBill"></partner-bill>
        </popup>
        
        <popup :is-set-styled="true" :show="popups.invoiceWaitPay.show" :name="popups.invoiceWaitPay.name" :width="992">
            <invoice-wait-pay></invoice-wait-pay>
        </popup>
        
        
        <popup :is-set-styled="true" :show="popups.invoiceWaitDocs.show" :name="popups.invoiceWaitDocs.name" :width="992">
            <invoice-wait-docs></invoice-wait-docs>
        </popup>
        
        
        <popup :is-set-styled="true" :show="popups.invoicePaid.show" :name="popups.invoicePaid.name" :width="992">
            <invoice-paid></invoice-paid>
        </popup>
        
    </div>
</template>



<script>
    import Popup from '@/components/modal/Popup.vue';
    import {mapState} from 'vuex';
    import InvoiceWaitPay from '@/components/modal/InvoiceWaitPay.vue';
    import InvoiceWaitDocs from '@/components/modal/InvoiceWaitDocs.vue';
    import InvoicePaid from '@/components/modal/InvoicePaid.vue';
    import PartnerBill from '@/components/modal/PartnerBill.vue';
    import Loader from '@/components/Loader.vue';

    export default {
        metaInfo() {
            return {
                title: 'Выставленные счета',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "buhInvoices",
        components: {
            Popup,
            InvoiceWaitPay,
            InvoiceWaitDocs,
            InvoicePaid,
            PartnerBill,
            Loader
        },
        data: function() {
            return {
                currentBill: {}
            }
        },
        created(){
            this.$store.dispatch('partnerCoupons/getBillItems');
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                bills: state => state.partnerCoupons.bills.items,
                isLoad: state => state.partnerCoupons.isLoad,
                user: state => state.users.user,
            }),
        },
        methods: {
            openPartnerBill: function (currentBill) {
                this.currentBill = currentBill;
                this.$store.dispatch('popups/open', {name: this.popups.partnerBill.name});
            },
            openInvoiceWaitPay: function (currentBill) {
                this.$store.dispatch('popups/open', {name: this.popups.invoiceWaitPay.name});
            },
            openInvoiceWaitDocs: function (currentBill) {
                this.$store.dispatch('popups/open', {name: this.popups.invoiceWaitDocs.name});
            },
            openInvoicePaid: function (currentBill) {
                this.$store.dispatch('popups/open', {name: this.popups.invoicePaid.name});
            },
        },
    }
</script>