<template>
    <div>
        <div v-if="isLoad || opinions.length === 0" class="wrapper-content">
            <div class="sect-blank">
                <div class="wrap text-center">
                    <loader v-if="isLoad"></loader>
                    <div v-else class="text-size-l text-bolder text-color-grey mt16">Здесь пока ничего нет...</div>

                </div>

                <div v-if="opinions.length === 0" class="mini-zems"></div>
            </div>
        </div>
        <div v-else class="opinions-list">
            <div v-for="opinion in opinions"
                 class="opinion-mod">
                <div class="opinion"
                     :class="{'opinion--processed':opinion.answer.text !== ''}">
                     
                    <span class="opinion__history i i-info" @click="openCouponHistory(opinion)"></span>
                     
                    <div class="opinion__author" :class="{'--dislike':opinion.type === 'negative'}">
                        <div class="rate">
                            <span class="i i-like"></span>
                        </div>

                        <div class="wrap">
                            <div class="wrap__top">
                                <div class="name">{{ opinion.user.name }}</div>

                                <div class="date">({{ getCityName(opinion.city)}}) {{ opinion.date }}</div>
                            </div>


                            <div class="wrap__text">
                                <p v-html="opinion.text"></p>
                            </div>
                        </div>
                    </div>
                    <!-- end opinion__author -->
                    
                    
                    
                    
                    
                    <div class="opinion__feedback" v-if="opinion.answer.text !== ''">
                        <div class="rate">
                            <span class="i i-pointer3"></span>
                        </div>

                        <div class="wrap">
                            <div class="wrap__top">
                                <div class="name" v-html="opinion.provider.name"></div>

                                <div class="date">Через {{ opinion.answer.date }}</div>
                            </div>


                            <div class="wrap__text">
                                <p v-html="opinion.answer.text"></p>
                            </div>
                        </div>
                    </div>
                    
                    
                    
                    <div class="opinion__feedback-mod" v-else-if="user.permission.indexOf(16) !== -1">
                        <a @click="toggleForm(opinion)" href="javascript:void(0);" class="text-size-s">Ответить</a>

                        <div v-if="opinion.form.show" class="mt16">
                            <textarea-field @focus="removeError(opinion)"
                                            @input="setComment($event, opinion)"
                                            :value="opinion.form.comment"
                                            :is-error="opinion.form.commentError"
                                            :placeholder="'Напишите ответ покупателю'"
                                            :add-style="{minHeight: 88 + 'px'}"></textarea-field>
                        </div>
                        
                        <div v-if="opinion.form.show" class="row-b mt24">
                            <div class="row-b__col elem-pos-fix mr24">
                                <submit-btn tag="button" class="btn mr24" @click.native="send(opinion)" :type="opinion.form.btnType">Опубликовать</submit-btn>
                                
                                <a @click="toggleForm(opinion)" href="javascript:void(0);" class="btn btn--white">Отмена</a>
                            </div>
                            
                            <div class="row-b__col elem-pos-fit">
                                <span class="text-size-s text-color-grey">
                                    Внимание! Написать ответ на отзыв можно<br>
                                    только один раз и изменить его после уже нельзя!
                                </span>
                            </div>
                        </div>
                    </div>
                    <!-- end opinion__author -->
                </div>
                <!-- end opinion -->
                
                
                
                <div class="opinion-mod__coupon">
                    <div class="wrap">
                        <div class="img" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+opinion.action.image+')' }"></div>
                        
                        <div class="text">
                            {{ opinion.action.name }}
                        </div>
                    </div>
                </div>
            </div>
            <!-- end opinion-mod -->
        </div>
        
        
        


        
        
        <popup :is-set-styled="true" :show="popups.couponHistory.show" :name="popups.couponHistory.name">
            <coupon-history :is-partner="true" :coupon="itemCurrent.coupon"></coupon-history>
        </popup>
    </div>

</template>

<script>
    import {mapState} from 'vuex';
    import TextareaField from '@/components/forms/TextareaField.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'
    import CouponHistory from '@/components/modal/CouponHistory.vue';
    import Loader from '@/components/Loader.vue';
    import Popup from '@/components/modal/Popup.vue';

    export default {
        name: "OpinionList",
        components: {
            TextareaField,
            SubmitBtn,
            CouponHistory,
            Loader,
            Popup
        },
        props: {
            actionId: {
                default() {
                    return 0
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                opinions: state => state.opinions.items,
                isLoad: state => state.opinions.isLoad,
                user: state => state.users.user,
            }),
        },
        data(){
            return {
                itemCurrent: {},
            }
        },
        mounted() {
            this.$store.commit('opinions/setData', {items: []});
            this.$store.commit('opinions/isLoadNextPage', true);
            this.$store.dispatch('opinions/getListPartner');
        },
        methods: {
            getCityName(xmlId) {
                let city = this.$store.getters['cities/getByXmlId'](xmlId)
                return city.name;
            },
            send(opinion) {
                let error = false;
                this.$store.commit('opinions/setError',{id:opinion.id, error: false});
                if(opinion.form.comment.length < 10){
                    setTimeout(()=>{
                        this.$store.commit('opinions/setError',{id:opinion.id, error: true});
                    },150);
                    error = true;
                }
                if(!error) {
                    this.$store.commit('opinions/setButtonState',{id:opinion.id, state: 'load'});
                    this.$store.dispatch('opinions/sendAnswer',{id:opinion.id, text: opinion.form.comment}).then(() => {
                        this.$store.commit('opinions/setButtonState',{id:opinion.id, state: 'ok'});
                        setTimeout(() => {
                            this.$store.commit('opinions/setButtonState',{id:opinion.id, state: 'normal'});
                        }, 300);
                    });
                }
            },
            openCouponHistory: function (item) {
                this.itemCurrent = item;
                this.$store.dispatch('popups/open', {name: this.popups.couponHistory.name});
            },
            toggleForm(opinion) {
                this.$store.commit('opinions/toggleForm',{id:opinion.id, show: true});
            },
            removeError(opinion) {
                this.$store.commit('opinions/setError',{id:opinion.id, show: false});
            },
            setComment(value,opinion) {
                this.$store.commit('opinions/setComment',{id:opinion.id, value: value});
            },
        }
    }
</script>