<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1">Погашенные купоны</div>
            </div>
            <div class="main-title__right">
                <span class="i i-info i--dark-trans mr4"></span>
                <span class="t text-color-grey">Купоны, за которые вам предстоит выплатить комиссию проекта</span>
            </div>
        </div>




        <section class="container" v-if="user.permission.indexOf(9) !== -1">
            
            <div class="container__full">
                
                <div class="buh-coupons" v-if="coupons.length > 0">
                    <div class="item">
                        <div class="item__title">
                            <span class="item__buyer">покупатель</span>
                            <span class="item__paid">погашен</span>
                            <span class="item__sum">сумма покупки</span>
                            <span class="item__discount">скидка</span>
                            <span class="item__transfer">поступило в кассу</span>
                            <span class="item__percent">комиссия 3%</span>
                        </div>
                    </div>
                    
                    
                    <div class="item" v-for="coupon in coupons">
                        <div class="item__link">
                            <span class="item__buyer">{{ coupon.phone }}</span>
                            <span class="item__paid">{{ coupon.date.pay }}</span>
                            <span class="item__sum">{{ coupon.price }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            <span class="item__discount">{{ coupon.discount }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            <span class="item__transfer">{{ coupon.transfer }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            <span class="item__percent">{{ coupon.our }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                        </div>
                    </div>
                    
                    <div class="item" v-if="coupons.length > 1">
                        <div class="item__total">
                            Общая комиссия за <strong>{{ coupons.length }}</strong> {{declOfNumber(coupons.length ,['купон','купона','купонов'])}}: <strong>{{ number_format(totalCommission) }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                        </div>
                    </div>
                </div>
                <!-- end buh-coupons -->
                

                <div class="wrapper-content mt32" v-else>
                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <loader v-if="isLoad"></loader>
                            <div class="text-size-l text-bolder text-color-grey mt16" v-else>У вас нет купонов, по которым ещё не выставлены счета...</div>
                        </div>
                        
                        <div class="mini-zems"></div>
                    </div>
                    <!-- end sect-blank -->
                </div>
                
            </div>
            <!-- end container__full -->
            
        </section>
        <!-- end container -->
        <section class="container" v-if="user.permission.length > 0 && user.permission.indexOf(9) === -1 ">
            <div class="container__full">
                <div class="wrapper-content">
                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <div class="text-size-l text-bolder text-color-red mt16">
                                У вас нет прав <br> доступа в этот раздел
                            </div>
                        </div>
                    </div>
                    <!-- end sect-blank -->
                </div>
            </div>
        </section>
        
    </div>
</template>



<script>
    import { mapState } from 'vuex'
    import {number_format} from '@/helpers/formatted.js';
    import {declOfNumber} from '@/helpers/formatted.js';
    import Loader from '@/components/Loader.vue';

    export default {
        metaInfo() {
            return {
                title: 'Новые купоны',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "buhNew",
        components: {
            Loader
        },
        data: function() {
            return {
            }
        },
        created(){
            this.$store.dispatch('partnerCoupons/getCommissionItems');
        },
        computed: {
            ...mapState({
                coupons: state => state.partnerCoupons.commission.items,
                isLoad: state => state.partnerCoupons.isLoad,
                user: state => state.users.user,
            }),
           totalCommission () {
                return this.$store.getters['partnerCoupons/getTotalCommission'];
            },
        },
        methods: {
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
            declOfNumber(number, titles){
                return declOfNumber(number, titles);
            },
        },
    }
</script>