<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1"><span>Отзывы</span>
                    <span class="ml32" v-if="!isLoad  && user.permission.indexOf(16) === -1">
                        <span class="i i-like i--dark-trans mt-2" style="transform: scale(0.75)"></span>
                        <span style="font-weight: normal;" class="t text-color-grey text-size-s mr16">{{ positive ? positive : 0 }}</span>


                        <span class="i i-like i--dark-trans" style="transform: scale(0.75) rotate(180deg)"></span>
                        <span style="font-weight: normal;" class="t text-color-grey text-size-s">{{ negative ? negative : 0 }}</span>
                    </span>
                </div>
            </div>
            
            <div class="main-title__right --icon" v-if="!isLoad  && user.permission.indexOf(16) !== -1">
                <span class="i i-like i--dark-trans mt-2" style="transform: scale(0.75)"></span>
                <span class="t text-color-grey text-size-s mr16">{{ positive ? positive : 0 }}</span>
                
                
                <span class="i i-like i--dark-trans" style="transform: scale(0.75) rotate(180deg)"></span>
                <span class="t text-color-grey text-size-s">{{ negative ? negative : 0 }}</span>
            </div>

            <div class="main-title__right" v-if="user.permission.indexOf(16) === -1">
                <span class="text-color-grey"><span class="i i-lock3 i--dark-trans mr4"></span>Вы не можете отвечать на отзывы от лица компании</span>
            </div>
        </div>


        <section class="container">
            <div class="container__full">
                <opinion-list v-if="cities.size > 0"></opinion-list>
            </div>
        </section>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import OpinionList from '@/components/partner/OpinionList.vue';

    export default {
        metaInfo() {
            return {
                title: 'Отзывы',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Opinion",
        components: {
            OpinionList
        },
        created() {
        },
        mounted() {
        },
        computed: {
            ...mapState({
                provider:  state => state.partner.current,
                cities:  state => state.cities.items,
                positive: state => state.opinions.positive,
                negative: state => state.opinions.negative,
                isLoad: state => state.opinions.isLoad,
                user: state => state.users.user,
            }),
        },
    }
</script>

